import React, { useState, useEffect } from "react";
import firebase from "gatsby-plugin-firebase"

import { userCraft } from "./login"

import Button from 'react-bootstrap/Button'

const isBrowser = typeof window !== `undefined`;

function getUserCraftData() {
  return userCraft.keys().then(function (keys) {
    return Promise.all(keys.map(function (key) {
      return userCraft.getItem(key)
        .then(function (value) {
          return { key: key, value: value };
        });
    }));
  });
}


function Logout(props) {
  const [isLoggingOut, setLoggingOut] = useState(null);

  if (isLoggingOut) { return <ConfirmLogout {...props} /> }
  return <Button variant={isLoggingOut ? "secondary" : "outline-light"} size="sm" onClick={() => setLoggingOut(true)}>Logout</Button>
}

export default Logout

function ConfirmLogout(props) {
  const [userCraftLogin, setUserCraftLogin] = useState(null);
  const [userCrafts, setUserCrafts] = useState(null);

  const [savingUserCrafts, setSavingUserCrafts] = useState(false);

  useEffect(() => {
    async function getUserData() {
      //     // You can await here
      let userCraftLogin = await userCraft.getItem('login');
      setUserCraftLogin(userCraftLogin);
      let memoryCrafts = await getUserCraftData();
      setUserCrafts(memoryCrafts);
    }
    getUserData();
  }, []);



  function doLogout() {
    let user = userCraftLogin.replace(/[^a-zA-Z0-9]/g, "_");
    setSavingUserCrafts(true);
  //  console.log(userCrafts);
    let myCrafts = {};
    userCrafts.forEach(element => {
    //  console.log(element);
      myCrafts[element.key] = element.value;
      // myCrafts[element]=memoryCrafts[element]
    });
  //  console.log(myCrafts);
    firebase.database().ref(`usersCraft/${user}/`).update(myCrafts).then(() => {
      setSavingUserCrafts(false);
      props.setUserCraftLogin(null);
      userCraft.clear().then(function () {
        // Run this code once the database has been entirely deleted.
        firebase.auth().signOut().then(() => console.log('Logged out'));
      }).catch(function (err) {
        // This code runs if there were any errors
        console.log(err);
      });

    })

  }

 // console.log(userCrafts, userCraftLogin);

  if (!isBrowser) return null

  return <Button variant={savingUserCrafts ? "outline-secondary" : "outline-secondary"} size="sm" onClick={doLogout}>{savingUserCrafts ? "Deleting data, saving in cloud" : "Delete local data and Logout"}</Button>
}





